.inter-cont {
    display: flex;
    justify-content: center;
}

.item-img {
    margin: 0.5rem;
    width: 22rem;
}
.inter-item:hover {
    background-color: rgba(0, 0, 0, 0.5);
}

@media (max-width: 800px) {
    .inter-cont {
      flex-direction: column;
      align-items: center;
    }
  }